import React from "react";
import { useState } from "react";
import { v4 as uuidv4 } from "uuid";
import "./crearTarea.css";

const CrearTarea = (props) => {
  const añadirTarea = props.onCrearTarea;
  const [nuevoNombreTarea, setNuevoNombreTarea] = useState("");
  const [nuevaPrioridadTarea, setNuevaPrioridadTarea] = useState("alta");
  const [nuevoTipoTarea, setNuevoTipoTarea] = useState("personal");
  const [nuevaFechaTarea, setNuevaFechaTarea] = useState(new Date());
  const [errorTarea, setErrorTarea] = useState(false);

  const crearTarea = (e) => {
    e.preventDefault();
    setErrorTarea(false);

    if (nuevoNombreTarea === "" || nuevaFechaTarea === "") {
      setErrorTarea(true);
      return;
    }

    const nuevaTareaCreada = {
      id: uuidv4(),
      nombre_tarea: nuevoNombreTarea,
      fecha: nuevaFechaTarea,
      tipo: nuevoTipoTarea,
      prioridad: nuevaPrioridadTarea,
    };
    añadirTarea(nuevaTareaCreada);
    setNuevoNombreTarea("");
    setNuevaFechaTarea("");
  };

  const cambiarNombreTarea = (e) => {
    setNuevoNombreTarea(e.target.value);
  };
  const cambiarFechaTarea = (e) => {
    setNuevaFechaTarea(e.target.value);
  };
  const cambiarTipoTarea = (e) => {
    setNuevoTipoTarea(e.target.value);
  };

  const cambiarPrioridadTarea = (e) => {
    setNuevaPrioridadTarea(e.target.value);
  };

  return (
    <div className="form-tarea col-md-5 col-12">
      <h1 className="text-center mt-3">TO-DO LIST</h1>
      <form
        action=""
        onSubmit={crearTarea}
        className="col-8 mt-2 justify-content-center mx-auto form-inline"
      >
        <div className="col-auto">
          <input
            type="text"
            name="tarea"
            id="tarea"
            value={nuevoNombreTarea}
            onChange={cambiarNombreTarea}
            placeholder="Tarea"
            className="form-control mt-2 col-auto"
          />
        </div>
        <div className="col-auto">
          <input
            type="datetime-local"
            name="fechaTarea"
            id="fechaTarea"
            value={nuevaFechaTarea}
            onChange={cambiarFechaTarea}
            placeholder="Fecha Tarea"
            className="form-control mt-2"
          />
        </div>
        <div className="col-12 d-flex">
          <select
            name="tipoTarea"
            id="tipoTarea"
            onChange={cambiarTipoTarea}
            value={nuevoTipoTarea}
            className="form-select mt-2 col-5 d-inline selectCrear"
          >
            <option value="personal">Personal</option>
            <option value="trabajo">Trabajo</option>
            <option value="ocio">Ocio</option>
          </select>
          <select
            name="prioridadTarea"
            id="prioridadTarea"
            onChange={cambiarPrioridadTarea}
            value={nuevaPrioridadTarea}
            className="form-select mt-2 col-5 d-inline selectCrear segundo"
          >
            <option value="alta">Alta</option>
            <option value="media">Media</option>
            <option value="baja">Baja</option>
          </select>
        </div>
        <div className="row mt-3">
          <div className="col-10 mx-auto d-flex justify-content-center">
            <input
              type="submit"
              className="btn btn-primary col-10 p-3"
              value="Crear Tarea"
            />
          </div>
        </div>
      </form>
      {errorTarea ? (
        <p className="errorTarea alert alert-danger col-10 mt-3 alertaForm">
          Tienes que rellenar todos los campos
        </p>
      ) : null}
    </div>
  );
};

export default CrearTarea;
