import "./App.css";
import CrearTarea from "./components/CrearTarea.jsx";
import Tareas from "./components/Tareas.jsx";
import { useState } from "react";

function App() {
  let listaTareas = [];
  if (JSON.parse(localStorage.getItem("listaTareas")) !== null) {
    listaTareas = JSON.parse(localStorage.getItem("listaTareas"));
  }

  const [tareas, setTarea] = useState(listaTareas);
  const crearTarea = (tarea) => {
    setTarea([...tareas, tarea]);
  };

  const borrarTarea = (idABorrar) => {
    var indexId = tareas.findIndex((tarea) => tarea.id === idABorrar);
    const copiaArray = [...tareas];
    copiaArray.splice(indexId, 1);
    return setTarea(copiaArray);
  };

  const modificarTarea = (
    idModificar,
    nombreNuevo,
    fechaNueva,
    tipoNuevo,
    prioridadNueva
  ) => {
    var indexId = tareas.findIndex((tarea) => tarea.id === idModificar);
    const copiaArray = [...tareas];
    copiaArray[indexId].nombre_tarea = nombreNuevo;
    copiaArray[indexId].fecha = fechaNueva;
    copiaArray[indexId].tipo = tipoNuevo;
    copiaArray[indexId].prioridad = prioridadNueva;
    return setTarea(copiaArray);
  };

  return (
    <div className="container-fluid">
      <div className="row">
        {localStorage.setItem("listaTareas", JSON.stringify(tareas))}
        <CrearTarea onCrearTarea={crearTarea} />
        <Tareas
          listaTareas={tareas}
          onBorrarTarea={borrarTarea}
          onModificarTarea={modificarTarea}
          
        />
      </div>
    </div>
  );
}

export default App;
