import React from "react";
import Tarea from "./Tarea.jsx";

const Tareas = (props) => {
  const tareas = props.listaTareas;
  const borrar = props.onBorrarTarea;
  const modificar = props.onModificarTarea;

  return (
    <div className="tareas col-md-7 col-12">
      <h1 className="mt-3 text-center">TAREAS</h1>
      {tareas === null
        ? null
        : tareas.map((tarea) => {
            return (
              <Tarea
                tarea={tarea}
                key={tarea.id}
                onBorrarTarea={borrar}
                onModificarTarea={modificar}
              />
            );
          })}
    </div>
  );
};

export default Tareas;
