import React, { useState } from "react";
import "./tarea.css";

const Tarea = (props) => {
  const tarea = props.tarea;
  const [modificar, setModificar] = useState(false);
  const borrar = props.onBorrarTarea;
  const modificarTarea = props.onModificarTarea;
  const idModificar = tarea.id;
  const [nombreNuevo, setNombreNuevo] = useState(tarea.nombre_tarea);
  const [prioridadNueva, setPrioridadNueva] = useState(tarea.prioridad);
  const [tipoNuevo, setTipoNuevo] = useState(tarea.tipo);
  const [fechaNueva, setFechaNueva] = useState(tarea.fecha);
  const [errorTarea, setErrorTarea] = useState(false);
  const fecha = new Date(tarea.fecha);
  const fechaFinal = `${(fecha.getDate() < 10 ? "0" : "") + fecha.getDate()}-${
    (fecha.getMonth() + 1 < 10 ? "0" : "") + (fecha.getMonth() + 1)
  }-${fecha.getFullYear()} ${
    (fecha.getHours() < 10 ? "0" : "") + fecha.getHours()
  }:${(fecha.getMinutes() < 10 ? "0" : "") + fecha.getMinutes()}`;

  const borrarID = (id) => {
    borrar(id);
  };
  const modificarTareaId = (e) => {
    e.preventDefault();

    setErrorTarea(false);

    if (nombreNuevo === "" || fechaNueva === "") {
      setErrorTarea(true);
      return;
    }
    setModificar(false);
    modificarTarea(
      idModificar,
      nombreNuevo,
      fechaNueva,
      tipoNuevo,
      prioridadNueva
    );
  };
  const abrirFormModificar = () => {
    setModificar(!modificar);
  };

  const cambiarNombreTarea = (e) => {
    setNombreNuevo(e.target.value);
  };
  const cambiarFechaTarea = (e) => {
    setFechaNueva(e.target.value);
  };
  const cambiarTipoTarea = (e) => {
    setTipoNuevo(e.target.value);
  };

  const cambiarPrioridadTarea = (e) => {
    setPrioridadNueva(e.target.value);
  };

  const añadirColor = () => {
    switch (tarea.prioridad) {
      case "alta":
        return "danger";
      case "media":
        return "warning";
      case "baja":
        return "success";
      default:
    }
  };
  const colorInteraccion = añadirColor();

  return (
    <div
      className={`alert col-md-10 col-12 d-flex p-md-4 text-center flex-wrap  alert-${colorInteraccion} tareaImprimir`}
      key={tarea.id}
    >
      <div className="datos col-10 d-flex">
        <div className="col-3">Tarea: {tarea.nombre_tarea}</div>
        <div className="col-3">Fecha: {fechaFinal}</div>
        <div className="col-3">Tipo: {tarea.tipo}</div>
        <div className="col-3">Prioridad: {tarea.prioridad}</div>
      </div>
      <div className="botones col-2">
        <i className="material-icons" onClick={abrirFormModificar}>
          edit
        </i>
        <i className="material-icons" onClick={() => borrarID(tarea.id)}>
          delete
        </i>
      </div>
      {modificar ? (
        <div className="col-12 formModificar">
          <form
            action=""
            id="form"
            className="mt-2 justify-content-between d-flex col-12 flex-wrap"
            onSubmit={modificarTareaId}
          >
            <div className="col-md-4 col-12 mt-2">
              <input
                type="text"
                name="tarea"
                id="tarea"
                placeholder="Tarea"
                className="form-control"
                value={nombreNuevo}
                onChange={cambiarNombreTarea}
              />
            </div>
            <div className="col-md-3 col-12 mt-2">
              <input
                type="datetime-local"
                name="fechaTarea"
                id="fechaTarea"
                placeholder="Fecha Tarea"
                className="form-control"
                value={fechaNueva}
                onChange={cambiarFechaTarea}
              />
            </div>
            <div className="col-md-2 col-12 mt-2">
              <select
                name="tipoTarea"
                id="tipoTarea"
                className="form-select"
                onChange={cambiarTipoTarea}
                value={tipoNuevo}
              >
                <option value="personal">Personal</option>
                <option value="trabajo">Trabajo</option>
                <option value="ocio">Ocio</option>
              </select>
            </div>
            <div className="col-md-2 col-12 mt-2">
              <select
                name="prioridadTarea"
                id="prioridadTarea"
                className="form-select"
                onChange={cambiarPrioridadTarea}
                value={prioridadNueva}
              >
                <option value="alta">Alta</option>
                <option value="media">Media</option>
                <option value="baja">Baja</option>
              </select>
            </div>
            <div className="col-auto mt-2">
              <input
                type="submit"
                className="btn btn-primary p-3"
                value="Modificar Tarea"
              />
            </div>
          </form>
          {errorTarea ? (
            <div
              className="bg-danger text-center alert text-white mt-4 col-auto mx-auto border border-danger"
              role="alert"
            >
              TIENES QUE RELLENAR TODOS LOS CAMPOS
            </div>
          ) : null}
        </div>
      ) : null}
    </div>
  );
};

export default Tarea;
